#cre {
    padding: 20px;
}

.product-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.product-card {
    border-radius: 8px;
    overflow: hidden;
    text-align: center;
    padding: 10px;
    background-color: #fff;
    height: 25rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.product-card:hover {
    cursor: pointer;
    transform: scale(1.5);
}

.product-image {
    width: 100%;
    height: 78%;
    object-fit: cover;
    padding-bottom: 0.3rem;
}

.product-name {
    font-size: 1.5rem;
    flex-shrink: 0;
    height: 7%;
    color: black !important;
}

.product-info {
    font-size: 1rem;
    color: #666;
    flex-shrink: 0;
    font-weight: 500;
}

.cre_detail {
    text-align: center;
    font-size: 7rem;
}

.cre_detailTop {
    margin-top: 0 !important;
}

/* ================================= */
.filter-select {
    width: 50%;
    display: block;
    margin: 20px auto;
    /* 위아래 여백과 중앙 정렬 */
    padding: 10px 15px;
    font-size: 16px;
    border: 2px solid #4CAF50;
    /* 테두리 색상 */
    border-radius: 5px;
    /* 모서리 둥글게 */
    background-color: #f9f9f9;
    /* 배경 색상 */
    color: #333;
    /* 글자 색상 */
    appearance: none;
    /* 기본 셀렉트 스타일 제거 */
    cursor: pointer;
    /* 포인터 커서 */
}

.filter-select:focus {
    outline: none;
    /* 포커스 시 테두리 제거 */
    border-color: #4CAF50;
    /* 포커스 시 테두리 색상 변경 */
    box-shadow: 0 0 5px rgba(76, 175, 80, 0.5);
    /* 그림자 효과 */
}

.filter-select::after {
    content: '▼';
    /* 드롭다운 화살표 */
    position: absolute;
    right: 15px;
    /* 오른쪽 여백 */
    top: 50%;
    transform: translateY(-50%);
    /* 세로 중앙 정렬 */
    pointer-events: none;
    /* 클릭 이벤트 방지 */
}